import axios from '../../utils/axios'
// 登录接口
export function loginApiFun(params) {
  return axios({
    url: '/user/login',
    method: 'post',
    params,
  })
}
// 注册接口
export function registerApiFun(params) {
  return axios({
    url: '/user/register',
    method: 'post',
    params,
  })
}
